const numberCircle = createSlice({
  name: 'number',
  initialState,
  reducers: {
    setNewDate: (state, action) => {
      state = action.payload;
    },
    setActiveIndex: (state, action) => {
      state.activeIndex = action.payload;
    },
  },
});

export const { setNewDate, setActiveIndex } = numberCircle.actions;
export default numberCircle.reducer;
